import React, { lazy, Suspense } from 'react';
import { View, Text, StyleSheet, Pressable } from 'react-native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { useNavigation } from '@react-navigation/native';
import { RootStackParamList, RootStackNavigationProp } from '../types/navigation';
import { SearchBar } from '../components/SearchBar';
import { LoadingIndicator } from '../components/LoadingIndicator';

// Lazy load screens
const BrowseScreen = lazy(() => import('../screens/BrowseScreen'));
const PrivacyPolicy = lazy(() => import('../screens/legal/PrivacyPolicy'));
const TermsOfService = lazy(() => import('../screens/legal/TermsOfService'));
const Contact = lazy(() => import('../screens/legal/Contact'));

const Stack = createNativeStackNavigator<RootStackParamList>();

interface AppNavigatorProps {
  searchQuery: string;
  onSearch: (text: string) => void;
}

export const AppNavigator = ({ searchQuery, onSearch }: AppNavigatorProps) => {
  const navigation = useNavigation<RootStackNavigationProp>();

  const handleLogoPress = () => {
    navigation.reset({
      index: 0,
      routes: [{ name: 'Browse', params: { searchQuery } }],
    });
  };

  return (
    <Stack.Navigator>
      <Stack.Screen 
        name="Browse"
        options={{
          headerTitle: '',
          headerLeft: () => (
            <View style={styles.headerContainer}>
              <Pressable 
                onPress={handleLogoPress}
                style={({ pressed }) => [
                  styles.logoContainer,
                  pressed && styles.logoPressed
                ]}
              >
                <Text style={styles.headerTitle}>FoilQuiver</Text>
              </Pressable>
              <View style={styles.searchBarContainer}>
                <SearchBar 
                  value={searchQuery}
                  onChangeText={onSearch}
                  onClear={() => onSearch('')}
                />
              </View>
            </View>
          ),
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <BrowseScreen {...props} />
          </Suspense>
        )}
      </Stack.Screen>
      <Stack.Screen 
        name="Terms"
        options={{
          title: 'Terms of Service',
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <TermsOfService {...props} />
          </Suspense>
        )}
      </Stack.Screen>
      <Stack.Screen 
        name="Privacy"
        options={{
          title: 'Privacy Policy',
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <PrivacyPolicy {...props} />
          </Suspense>
        )}
      </Stack.Screen>
      <Stack.Screen 
        name="Contact"
        options={{
          title: 'Contact Us',
          headerStyle: {
            backgroundColor: '#f4f4f4',
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <Contact {...props} />
          </Suspense>
        )}
      </Stack.Screen>
    </Stack.Navigator>
  );
};

const styles = StyleSheet.create({
  headerContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    flex: 1,
    paddingRight: 16,
    paddingLeft: 16,
  },
  headerTitle: {
    fontSize: 20,
    fontWeight: 'bold',
    marginRight: 16,
  },
  searchBarContainer: {
    flex: 1,
  },
  logoContainer: {
    padding: 8,
    marginLeft: -8,
  },
  logoPressed: {
    opacity: 0.7,
  },
}); 